const months = {
    nominative: ['', '', '', '', '', '', '', '', '', '', '', ''],
    accusative: ['', '', '', '', '', '', '', '', '', '', '', '']
};

const weekdays = {
    long: ['', '', '', '', '', '', ''],
    short: ['', '', '', '', '', '', '']
};

export function formatDate(date) {
 
    let dd = date.getDate();
    if (dd < 10) dd = '0' + dd;
  
    let mm = date.getMonth() + 1;
    if (mm < 10) mm = '0' + mm;
  
    let yyyy = date.getFullYear();
      
    return yyyy + '-' + mm + '-' + dd;
}

export function formatDateAsLongString(date) {
    return `${date.getDate()} ${months.accusative[date.getMonth()]}, ${date.getFullYear()}`;
}

export function formatDateAsShortString(date) {
    return `${date.getDate()}, ${weekdays.short[date.getDay()]}`;
}

export function getMonthName(date) {
    return months.nominative[date.getMonth()];
}